import validateCPF from "./validateCpf";
import convertHourToMinutes from "./convertHourToMinute";

interface MedicError {
  firstName: string;
  lastName: string;
  email: string;
  password: string;
  confirmPassword: string;
  phoneNumber: string;
  area: string;
  graduation: string;
  masterDegree: string;
  doctorateDegree: string;
  cpf: string;
  rg: string;
  birthDate: string;
  crm: string;
  schedule: Array<string>;
}

interface Medic {
  firstName: string;
  lastName: string;
  email: string;
  password: string;
  confirmPassword: string;
  phoneNumber: string;
  area: string;
  graduation: string;
  masterDegree: string;
  doctorateDegree: string;
  cpf: string;
  rg: string;
  birthDate: string;
  crm: string;
  schedule: Array<Schedule>;
}

interface Schedule {
  week_day: number;
  from: string;
  to: string;
}

export default function validateMedicInfo(credentials?: Medic) {
  let errors = { schedule: new Array<string>() } as MedicError;

  if (credentials !== null) {
    if (!credentials?.firstName?.length ?? 0) {
      errors.firstName = "Campo de nome  necessrio";
    }

    if (!credentials?.lastName?.length ?? 0) {
      errors.lastName = "Campo de sobrenome  necessrio";
    }

    if (!credentials?.email?.length ?? 0) {
      errors.email = "Campo de e-mail  necessrio";
    } else if (
      !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(credentials?.email ?? "")
    ) {
      errors.email = "E-mail invlido";
    }

    if (!credentials?.password?.length ?? 0) {
      errors.password = "Campo de senha  necessrio";
    } else if (credentials?.password?.length < 6) {
      errors.password = "A senha precisa de 6 ou mais caracteres";
    }

    if (!credentials?.confirmPassword?.length ?? 0) {
      errors.confirmPassword = "Campo de confirmao de senha  necessrio";
    } else if (credentials?.confirmPassword !== credentials?.password) {
      errors.confirmPassword = "As senhas no combinam";
    }

    const phoneNumbers =
      credentials?.phoneNumber?.replace(/[-.() ]/g, "") ?? "0";

    if (isNaN(parseInt(phoneNumbers))) {
      errors.phoneNumber = "Nmero de telefone invlido";
    } else if (phoneNumbers.length !== 11) {
      errors.phoneNumber = "Nmero de telefone invlido";
    } else if (!phoneNumbers.length ?? 0) {
      errors.phoneNumber = "Campo de telefone  necessrio";
    }

    if (!credentials?.area?.length ?? 0) {
      errors.area = "Campo de rea  necessrio";
    }

    if (!credentials?.graduation?.length ?? 0) {
      errors.graduation = "Campo de graduao  necessrio";
    } else if ((credentials?.graduation?.length ?? 0) < 4) {
      errors.graduation = "Campo de graduao com poucos caracteres";
    }

    if (
      (credentials?.masterDegree?.length ?? 0) >= 1 &&
      (credentials?.masterDegree?.length ?? 0) <= 4
    ) {
      errors.masterDegree = "Campo de mestrado com poucos caracteres";
    }

    if (
      (credentials?.doctorateDegree?.length ?? 0) >= 1 &&
      (credentials?.doctorateDegree?.length ?? 0) <= 4
    ) {
      errors.doctorateDegree = "Campo de doutorado com poucos caracteres";
    }

    if (!credentials?.crm?.length ?? 0) {
      errors.crm = "Campo de CRM  necessrio";
    }

    const cpfNumbers = credentials?.cpf?.replace(/[-. ]/g, "") ?? "0";

    if (!cpfNumbers?.length ?? 0) {
      errors.cpf = "Campo de CPF  necessrio";
    } else if (isNaN(parseInt(cpfNumbers))) {
      errors.rg = "Caracteres no aceitos";
    } else if (validateCPF({ cpf: cpfNumbers })) {
      errors.cpf = "CPF invlido";
    }
    const rgNumbers = credentials?.rg?.replace(/[-. ]/g, "") ?? "0";

    if (!rgNumbers?.length ?? 0) {
      errors.rg = "Campo de RG  necessrio";
    } else if (rgNumbers?.length !== 9) {
      errors.rg = "RG invlido";
    } else if (isNaN(parseInt(rgNumbers))) {
      errors.rg = "Caracteres no aceitos";
    }

    if (!credentials?.birthDate?.length ?? 0) {
      errors.birthDate = "Campo de data de nascimento  necessrio";
    } else {
      let year = new Date(credentials?.birthDate).getFullYear();
      let month = new Date(credentials?.birthDate).getMonth() + 1;
      let day = new Date(credentials?.birthDate).getDate();
      if (new Date().getFullYear() - year >= 18) {
        if (new Date().getFullYear() - year === 18) {
          if (new Date().getMonth() + 1 - month >= 0) {
            if (new Date().getDate() - day <= 0) {
              errors.birthDate = "Voc precisa ser maior de 18 anos";
            }
          } else {
            errors.birthDate = "Voc precisa ser maior de 18 anos";
          }
        }
      } else {
        errors.birthDate = "Voc precisa ser maior de 18 anos";
      }
    }

    if (!credentials?.schedule?.length ?? 0) {
      if (errors.schedule.length > 0) {
        errors.schedule[0] =
          "Voc precisa informar pelo menos um dia da semana.";
      } else {
        errors.schedule.push(
          "Voc precisa informar pelo menos um dia da semana."
        );
      }
    } else if (typeof credentials.schedule !== "string") {
      let i = 0;
      for (let appointment of credentials?.schedule) {
        if (appointment.from.length === 0 || appointment.to.length === 0) {
          if (errors?.schedule?.length < i) {
            errors.schedule[i] = "Campo no pode ser vazio";
          } else {
            errors.schedule.push("Campo no pode ser vazio");
          }
        } else {
          const from = convertHourToMinutes(appointment.from);
          const to = convertHourToMinutes(appointment.to);

          if (from > to) {
            if (errors?.schedule?.length < i) {
              errors.schedule[i] = "Carga horria invlida";
            } else {
              errors.schedule.push("Carga horria invlida");
            }
          }
        }
      }
    }
  }

  return errors;
}
